document.addEventListener("DOMContentLoaded", () => {
	// Includes
	// // Burger
class Burger {
	constructor(btn, list) {
		this.btn = btn;
		this.list = list;
		this.opened = false;
		this.btnDisabled = false;
		this.scrollWidth = window.innerWidth - document.body.clientWidth;

		this.btn.addEventListener('click', () => {
			this.toggle();
		})
	}

	open() {
		this.disableBtn();
		if (this.scrollWidth) {
			document.body.style.paddingRight = `${this.scrollWidth}px`
		}
		this.btn.classList.add('burger--active');
		this.btn.setAttribute('aria-expanded', true);
		this.list.classList.add('nav--active');
		this.list.setAttribute('aria-hidden', false);

		body.classList.add('lock')
	}

	close() {
		this.disableBtn();
		if (this.scrollWidth) {
			document.body.style.paddingRight = null;
		}
		body.classList.remove('lock')
		this.btn.classList.remove('burger--active');
		this.btn.setAttribute('aria-expanded', false);
		this.list.classList.remove('nav--active');
		this.list.setAttribute('aria-hidden', true);
	}

	toggle() {
		if (this.btn.classList.contains('burger--active')) {
			this.close();
		} else {
			this.open();
		}
	}

	disableBtn() {
		this.btn.disabled = true;
		this.list.addEventListener('transitionend', () => {
			this.btn.disabled = false;
		})
	}
};
	// // QA

class Qa {
	constructor(selector) {
		this.qaContainer = selector;
		this.btns = this.qaContainer.querySelectorAll('.topics__btn');
		this.items = this.qaContainer.querySelectorAll(`.qa-item`);
		this.preview = this.qaContainer.querySelector('.qa__topics-line');
		this.previewElements = ``;
		this.previewItems = null;
		this.prevTargets = null;
	}

	insertPreviewElements() {
		this.btns.forEach((el) => {
			this.previewElements += `
			<span class="qa__topic" data-path="${el.dataset.path}">
					${el.textContent}</span>
			`
		});
		this.preview.innerHTML = this.previewElements;
		this.previewItems = this.preview.querySelectorAll(`.qa__topic`);
	}
	getPath(target) {
		return target.dataset.path;
	}
	setClasses(target, path) {
		target.classList.add('topics__btn--active');
		target.disabled = true;
		target.ariaExpanded = true;
		document.querySelector(`.qa-item[data-path="${path}"]`).classList.add('qa-item--active');
		this.preview.querySelector(`.qa__topic[data-path="${path}"]`).classList.add('qa__topic--active');
	}

	computePreviewTranslateWidth(path) {
		let previewElementsWidth = 0;
		for (let index = 0; index < this.previewItems.length; index++) {
			const element = this.previewItems[index];
			if (element.dataset.path === path) {
				break;
			}
			previewElementsWidth += element.offsetWidth;
		}
		return previewElementsWidth;
	}

	setPrevTargetObj(target, path) {
		this.prevTargets = {
			btn: target,
			line: this.preview.querySelector(`.qa__topic[data-path="${path}"]`),
			question: this.qaContainer.querySelector(`.qa-item[data-path="${path}"]`)
		}
	}

	removePrevClasses() {
		if (this.prevTargets) {
			this.prevTargets.btn.classList.remove('topics__btn--active');
			this.prevTargets.btn.ariaExpanded = false;
			this.prevTargets.btn.disabled = false;
			this.prevTargets.line.classList.remove('qa__topic--active');
			this.prevTargets.question.classList.remove('qa-item--active');
		}
	}

	showTarget(target) {
		const path = this.getPath(target);
		this.items.forEach((el) => {
			el.style.transform = `translateX(-${parseInt(path) - 1}00%)`;
		});
		this.setClasses(target, path);
		this.preview.style.transform = `translateX(-${this.computePreviewTranslateWidth(path)}px)`;
		this.removePrevClasses();
		this.setPrevTargetObj(target, path);
	}

	init() {
		this.insertPreviewElements();
		this.showTarget(this.qaContainer.querySelector('.topics__btn[data-path="1"]'));
	}
}
;
	// // Debounce
class Debounce {
	constructor(time = 100, functions = () => { }) {
		this.time = time;
		this.functions = functions;
		this.timeOut = null;
	}

	init() {
		clearTimeout(this.timeOut);
		this.timeOut = setTimeout(() => {
			this.functions();
		}, this.time);
	}
}
;
	// // Variables
const isMobileDevice = (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent));
const isSmoothScrollSupported = 'scrollBehavior' in document.documentElement.style;
const media = window.matchMedia("(max-width: 767px)");

const body = document.querySelector('.body');
const burgerBtn = document.querySelector('.burger');
const headerNav = document.querySelector('.nav');
const qa = document.querySelector('.qa__container');
const toTopBtn = document.querySelector('.footer__btn');

const burgerCl = new Burger(burgerBtn, headerNav);
const qaCl = new Qa(qa);
const ScrollDebounce = new Debounce(100, () => {
	showActiveLink(document.querySelectorAll('.nav__link'), 'nav__link--active', media.matches ? 55 : 85);
});
;
	// // Functions
function showActiveLink(links, activeClass, offsetDifference) {
	for (let i = links.length - 1; i >= 0; i--) {
		let target = document.querySelector(links[i].hash);
		if (window.scrollY > target.offsetTop - offsetDifference) {
			if (document.querySelector(`.${activeClass}`)) {
				document.querySelector(`.${activeClass}`).classList.remove(`${activeClass}`);
			}
			links[i].classList.add(`${activeClass}`);
			break;
		} else {
			if (document.querySelector(`.${activeClass}`)) {
				document.querySelector(`.${activeClass}`).classList.remove(`${activeClass}`);
			}
		}
	}
}

function scrollToTarget(id, media) {
	const target = document.querySelector(id);

	if (!isSmoothScrollSupported && target !== null) {
		const bodyRect = document.body.getBoundingClientRect();
		const elemRect = target.getBoundingClientRect();
		const offset = elemRect.top - bodyRect.top;

		const requestAnimFrame = (function () {
			return window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || function (callback) {
				window.setTimeout(callback, 1000 / 60);
			};
		})();
		Math.easeInOutQuad = function (t, b, c, d) {
			t /= d / 2;
			if (t < 1) {
				return c / 2 * t * t + b
			}
			t--;
			return -c / 2 * (t * (t - 2) - 1) + b;
		};
		function scrollToIos(to, callback, duration) {
			function move(amount) {
				document.documentElement.scrollTop = amount;
				document.body.parentNode.scrollTop = amount;
				document.body.scrollTop = amount;
			}

			function position() {
				return document.documentElement.scrollTop || document.body.parentNode.scrollTop || document.body.scrollTop;
			}
			let start = position(),
				change = to - start,
				currentTime = 0,
				increment = 20;
			duration = (typeof (duration) === 'undefined') ? 500 : duration;

			let animateScroll = function () {
				currentTime += increment;
				let val = Math.easeInOutQuad(currentTime, start, change, duration);
				move(val);
				if (currentTime < duration) {
					requestAnimFrame(animateScroll);
				} else {
					if (callback && typeof (callback) === 'function') {
						callback();
					}
				}
			};
			animateScroll();
		}
		scrollToIos(offset - 50, null, 500);
		return;
	}

	if (target !== null) {
		let pos = target.offsetTop - (media.matches ? 50 : 80);
		window.scrollTo({
			top: pos,
			behavior: 'smooth'
		});
	}
};
	// // scrollLockIOS
// Iphones
const isiPhone = (navigator.userAgent.match(/iPhone/i) != null);
const isiPad = (navigator.userAgent.match(/iPad/i) != null);
const isiPod = (navigator.userAgent.match(/iPod/i) != null);

// Выключение скролла
let disableScroll = () => {
	let pagePosition = window.scrollY;
	document.querySelector('html').style.scrollBehavior = 'auto';
	document.body.classList.add('ios-lock');
	document.body.dataset.position = pagePosition;
	document.body.style.top = -pagePosition + 'px';
}

// Включение скролла
let enableScroll = () => {
	let pagePosition = parseInt(document.body.dataset.position, 10);
	document.body.style.top = 'auto';
	document.body.classList.remove('ios-lock');
	window.scroll({ top: pagePosition, left: 0 });
	document.querySelector('html').removeAttribute('style');
	document.body.removeAttribute('data-position');
}

// Слушатель на один элемент
let scrollLock_BtnListener = (item) => {
	item.addEventListener('click', (e) => {
		item.classList.toggle('scroll')
		if (item.classList.contains('scroll')) {
			disableScroll();
		} else {
			enableScroll();
		}
	});
};
	// // Animate On Scroll
// class="animate-item" data-animation='from-left'  data-delay="0.5" data-animation-once
const animItems = document.querySelectorAll(".animate-item");
if (!isMobileDevice) {
	const elementInViewport = (element, offsetTop, offsetBottom, delay = 0) => {
		let bounds = element.getBoundingClientRect();
		if (element.dataset.delay) delay = element.dataset.delay;

		if ((bounds.top + bounds.height - bounds.height * offsetBottom > 0) && (window.innerHeight - bounds.top - bounds.height * offsetTop > 0)) {
			element.classList.add("animated");
			element.style.transitionDelay = `${delay}s`;
		} else if (!element.hasAttribute("data-animation-once")) {
			element.classList.remove("animated");
		}
	};
	if (animItems.length != 0) {
		animItems.forEach(el => {
			elementInViewport(el, .5, .5);
		})
		document.addEventListener('scroll', () => {
			animItems.forEach(el => {
				elementInViewport(el, .5, .5);
			});
		});
	};
} else {
	animItems.forEach((el) => {
		el.classList.add('animated')
	});
}
// -- //
;
	// -- //

	qa.addEventListener('click', (e) => {
		const target = e.target;
		if (target.classList.contains('topics__btn')) {
			qaCl.showTarget(target);
		}
	})

	headerNav.addEventListener('click', (e) => {
		const target = e.target;

		if (target.classList.contains('nav__link')) {
			e.preventDefault();
			let hash = target.hash;
			scrollToTarget(hash, media);
			media.matches ? burgerCl.close() : null;
		}
	})

	toTopBtn.addEventListener('click', () => {
		scrollToTarget('#hero', media);
	})

	qaCl.init();

	window.addEventListener('scroll', () => {
		ScrollDebounce.init();
	})


})
